import { IconText } from '@/components/common/IconText'
import LimitDetailsModal from '@/components/profile/Orders/LimitDetailsModal'
import useLimitCancelAction from '@/hooks/limit/useLimitCancelAction'
import useLimitClaimAction from '@/hooks/limit/useLimitClaimAction'
import { LimitOrderInfo } from '@/types/limit'
import { CetusTooltip } from '@cetus/design'
import useExplorer from '@cetus/hooks/src/useExplorer'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Icon } from '@cetus/ui-kit'
import { cancelBubble, d } from '@cetus/utils'
import { Button, Center, HStack, Text, VStack } from '@chakra-ui/react'
import { useState } from 'react'
import ClaimModal from '../ClaimModal'

export const OrderActionBlock = ({
  orderInfo,
  isProfile = false,
  isOpenOrder = true
}: {
  orderInfo: LimitOrderInfo
  isProfile?: boolean
  isOpenOrder?: boolean
}) => {
  const { handleCancelOrder, cancelOrderLoading } = useLimitCancelAction()
  const { handleClaimOrder, claimLoading, getLimitOrderClaimHistory, isClaimHistoryLoading } = useLimitClaimAction()
  const [actionOrder, setActionOrder] = useState<LimitOrderInfo | undefined>()
  const [isOpenClaimModal, setIsOpenClaimModal] = useState(false)
  const [isOpenTradeDetails, setIsOpenTradeDetails] = useState(false)
  const handleOrderAction = (order: LimitOrderInfo, action: 'cancel' | 'claim') => {
    setActionOrder(order)
    if (action === 'cancel') {
      handleCancelOrder([order])
    }
    if (action === 'claim') {
      // handleClaimOrder(order)
      setIsOpenClaimModal(true)
    }
  }
  const { isApp } = useWindowWidth()
  const { getExplorerUrl } = useExplorer()
  return (
    <HStack
      w={{
        base: 'calc(100vw - 32px)',
        lg: 'unset'
      }}
      pl={{
        base: '8px',
        lg: '0px'
      }}
      pr={{
        base: '8px',
        lg: '0px'
      }}
      justify={{
        base: 'space-between',
        lg: 'flex-end'
      }}
      gap="8px"
    >
      {isOpenOrder && (
        <HStack justify="flex-end">
          {d(orderInfo?.obtained_amount).lte(0) && d(orderInfo.un_claimed_amount).lte(0) ? (
            <CetusTooltip
              placement="top"
              tooltip={
                <Text fontSize="12px" lineHeight="20px" maxW="280px">
                  For a partially filled order, you can manually claim the filled amount if you want. Or you can wait for the auto distribution until
                  your order is fully filled, expired or cancelled.
                </Text>
              }
            >
              <Center
                w={{
                  base: 'calc(50vw - 28px)',
                  lg: 'unset'
                }}
              >
                <Button disabled={true} p="0 8px" h="28px" borderRadius="8px" fontSize="12px">
                  Claim
                </Button>
              </Center>
            </CetusTooltip>
          ) : (
            <Button
              isLoading={claimLoading}
              disabled={claimLoading}
              w={{
                base: '50%',
                lg: 'unset'
              }}
              h="28px"
              p="0 8px"
              borderRadius="8px"
              fontSize="12px"
              fontWeight="500"
              onClick={(e: any) => {
                cancelBubble(e)
                if (!claimLoading) {
                  handleOrderAction(orderInfo, 'claim')
                }
              }}
            >
              Claim
            </Button>
          )}
          {isProfile && (
            <CetusTooltip
              placement="bottom-end"
              tooltip={
                <VStack align="flex-start">
                  <IconText
                    text="View on Explorer"
                    onClick={() => {
                      window.open(getExplorerUrl(orderInfo.order_id, 'poolAddress'))
                    }}
                  />
                  <IconText
                    text={'Cancel'}
                    onClick={() => {
                      if (!cancelOrderLoading) {
                        handleOrderAction(orderInfo, 'cancel')
                      }
                    }}
                  />
                </VStack>
              }
            >
              <Center>
                <Button
                  variant="outline"
                  w="32px"
                  h="32px"
                  p="0"
                  borderRadius="8px"
                  sx={{
                    _hover: {
                      svg: {
                        fill: 'primary'
                      }
                    }
                  }}
                >
                  <Icon svgFill={isApp ? 'primary' : 'text_paragraph'} xlinkHref="#icon-icon_more" svgW="16px" svgH="16px" />
                </Button>
              </Center>
            </CetusTooltip>
          )}
        </HStack>
      )}

      {!isProfile && isOpenOrder && (
        <Button
          isLoading={cancelOrderLoading}
          disabled={cancelOrderLoading}
          w={{
            base: '50%',
            lg: 'unset'
          }}
          h="28px"
          p="0 8px"
          variant="outline"
          borderRadius="8px"
          fontSize="12px"
          fontWeight="500"
          onClick={(e: any) => {
            cancelBubble(e)
            if (!cancelOrderLoading) {
              handleOrderAction(orderInfo, 'cancel')
            }
          }}
        >
          Cancel
        </Button>
      )}

      {isProfile && !isOpenOrder && (
        <HStack>
          <Button
            h="32px"
            variant="outline"
            fontSize="12px"
            p="0 8px"
            borderRadius="8px"
            fontWeight="500"
            onClick={(e: any) => {
              cancelBubble(e)
              setIsOpenTradeDetails(true)
            }}
          >
            Details
          </Button>
          <Button
            onClick={(e: any) => {
              cancelBubble(e)
              window.open(getExplorerUrl(orderInfo.order_id, 'poolAddress'))
            }}
            w="32px"
            p="0"
            h="32px"
            fontSize="12px"
            borderRadius="8px"
            variant="outline"
            _hover={{
              svg: {
                fill: 'primary'
              }
            }}
          >
            <Icon svgW="20px" xlinkHref="#icon-icon_link1" />
          </Button>
        </HStack>
      )}
      {isOpenClaimModal && (
        <ClaimModal
          orderInfo={orderInfo}
          claimLoading={claimLoading}
          isOpen={isOpenClaimModal}
          onClose={() => setIsOpenClaimModal(false)}
          handleClaim={(claimValue: string | number) => handleClaimOrder(orderInfo, claimValue)}
          getLimitOrderClaimHistory={() => getLimitOrderClaimHistory(orderInfo)}
          isClaimHistoryLoading={isClaimHistoryLoading}
        />
      )}
      {isOpenTradeDetails && <LimitDetailsModal isOpen={isOpenTradeDetails} onClose={() => setIsOpenTradeDetails(false)} historyInfo={orderInfo} />}
    </HStack>
  )
}
